/*Sidebar Styles*/
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
.menuIcon {
    margin: 20px 85px;
    cursor: pointer;
}

.sideBar {
    display: flex;
    position: absolute;
    margin-left: 0px;
    flex-direction: column;
    top: 0px;
    left: 0px;
    height: 100%;
    background: none repeat scroll 0 0 white;
    color: white;
    font-family: "Open Sans";
    font-weight: bold;
    padding: 20px 10px 20px 0px;
    border-radius: 0px 15px 15px 0px;
    box-shadow: 0 2px 2px -2px rgba(0,0,0,.2);
}

.sideBar ul {
    margin: 0;
    padding: 0;
}

.sideBar ul a {
    text-decoration: none;
}

.sideBar ul a li {
    margin: 10px;
    padding: 10px 10px 10px 0px;
    margin-top: 10px;
    border-radius: 0px 15px 15px 0px;
    list-style-type: none;
    list-style-position: outside;
    text-decoration: none;
}

.sideBar ul a li{
    text-decoration: none;
    text-align: left;
    color: #3380ac;
    margin-left: 20px;
    margin-right: 12px;
    padding-left: 0px;
    font-size: 16px;
    position: relative;
} 

.sideBar ul a li:hover {
    background: #A78EC3;
    box-shadow: 0 20px 20px -2px rgba(0,0,0,.2);
    color:white;
    margin-left: 0;
    padding-left: 20px;
    text-decoration: none;
}

.sideBar ul a li:hover span {
    color: inherit;
}

.sideBar ul a:hover {
    text-decoration: none;
}

.sideBar ul a li{
    color: white;
    text-decoration: none;
    text-align: left;
    margin-left: 20px;
    margin-right: 12px;
}

/*Create Account Styles*/

.cardStyle2 {
    width: 40vw;
    min-height: 85vh;
    align-items: right;
    background-color: #F7F7FF;
    font-family: "Open Sans";
    box-shadow: none !important;
    margin: 0 2vw;
}

.logOutScreen{
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    align-items: center;
    justify-content: center;
}
.form-group:first-child{
    margin-top: 25px;
}
.btn-toolbar{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
@media screen and (max-width: 600px) {
    .cardStyle2{
        margin-top: 5vh;
        width: 90vw;
    }
    .form-group{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .form-group > .outlinedInput{
        width: 75vw !important;
    }
    .btn-outline-secondary{
        width: 17vw;
        padding: 5px 0;
        font-size: small;
    }
}
.cardTitleStyle {
    font-family: "Open Sans";
    font-weight: 600;
    font-size: 35px;
    text-align: center;
    margin: 10px auto;
}

.btnSignUp {
    background-color: #A78EC3;
    color: white;
    width: 200px;
    height: 50px;
    font-weight: 600;
    font-family: "Open Sans";
    box-shadow: none;
    border: none;
    border-radius: 10px;
    position: center;
}
.btnSignUp:hover{
    background-color: #7e6897;
}
